import ChatActions from '../actions/ChatActions';
import Constants from '../constants/Constants';
import SharedUtils from '../utils/shared';

export default class Entry extends React.Component {
  constructor(props) {
    super(props);

    Object.keys(this.props).forEach(function(key) {
      this[key] = this.props[key];
    });

    this.typing = false;
  }

  /*componentDidMount() {
  
  }*/

  setTypingStatus = () => {
    if (this.connected) {
      if (!this.typing) {
        this.typing = true;

        ChatActions.addMemberTyping(this.username);

        this.socket.emit(Constants.Sockets.Events.StartTyping, member);
      }

      const lastTypingTime = (new Date()).getTime();

      setTimeout(() => {
        const typingTimer = (new Date()).getTime();
        const timeDiff = typingTimer - lastTypingTime;

        if (timeDiff >= Constants.Sockets.Timings.Typings && this.typing) {
          this.typing = false;

          ChatActions.removeMemberTyping(this.username);

          this.socket.emit(Constants.Sockets.Events.EndTyping, this.username);
        }
      }, Constants.Sockets.Timings.Typings);
    }
  }

  onClickFocus = event => {
    // Focus input when clicking on the message input's border
    event.currentTarget.focus();
  }

  onInput = () => {
    this.setTypingStatus();
  }

  onKeyDown = event => {
    if (event.which === 13) {
      const message = {
        username: this.props.username,
        message: event.currentTarget.value,
        date: SharedUtils.getPrettyDate(new Date().toISOString())
      };

      ChatActions.addMessage(message);

      this.socket.emit(Constants.Sockets.Events.Message, message);
    }
  }

  render() {
    if (this.props.username) {
      return (
          <div className="chat page animated fadeOut">
              <div className="ms-SearchBox">
                  <input 
                      className="inputMessage ms-SearchBox-field" 
                      onClick={this.onClickFocus.bind(this)} 
                      onInput={this.onInput.bind(this)} 
                      onKeyDown={this.onKeyDown.bind(this)} 
                      placeholder="Enter your message..."/>
              </div>
          </div>
      );
    }
  }
}


