import Constants from '../constants/Constants';

function getTypingMessages(data) {
  const messageCollection = document.querySelectorAll('.typing.message');

  return [].slice.call(messageCollection).filter(function(el, i) {
    return el.dataset.username === data.username;
  });
}

export default class Message extends React.Component {
  constructor(props) {
    super(props);
    
    Object.keys(this.props).forEach(function(key) {
      this[key] = this.props[key];
    });
  }

  componentDidMount() {
    if (this.props.messages) {
      this.updateTimeStamps();
    }

    /*
  
    TODO:
      Setup listener on store to get messages
  
    */
  }

  ensureRemovalOfTypings() {
    const typingMessages = getTypingMessages(data);

    if (typingMessages.length !== 0) {
      typingMessages.forEach(function(typing) {
        typing.parentNode.removeChild(typing);
      });
    }
  }

  updateTimeStamps() {
    // every five minutes update the time stamps
    var stampInterval = setInterval(function() {
      // loop through the node list to get all dates
      [].slice.call(document.querySelectorAll('.prettyDateTime')).forEach(function(d, i) {
        d.innerText = getPrettyDate(d.dataset.datetime);
      });
    }, Constants.Messages.PrettyDate.RefreshFrequency);
  }

  writeMessage(data) {
    return (
       <div 
         className={`ms-ListItem is-selectable message ${data.typing ? 'typing' : ''} animated flipInY`} 
         data-username={data.username}>
           <span className="username ms-ListItem-primaryText">
             {data.username}
           </span>
           <span className="messageBody ms-ListItem-secondaryText">
             {data.message}
           </span>
           <span className="ms-ListItem-metaText prettyDateTime" data-datetime={data.date}>
             {getPrettyDate(data.date)}
           </span>
       </div>
    );
  }

  render() {
    return (
      <div className="messages">
        {this.state.messages.map(this.writeMessage)}
      </div>
    );
  }
}



