import AppDispatcher from '../dispatcher/AppDispatcher';
import ChatConstants from '../constants/ChatConstants';

function dispatch(data, actionType) {
  AppDispatcher.dispatch({
    actionType: actionType,
    results: data
  });
}

export default class ChatActions {
    addMessage(data) {
      dispatch(data, ChatConstants.MESSAGE);
      
      //Socket.emit(Constants.Sockets.Events.Message, data);
    }

    addMember(member) {
      dispatch(member, ChatConstants.PARTICIPANT_JOIN);
      
      //Socket.emit(Constants.Sockets.Events.Joiner, member);
    }

    removeMember(member) {
      dispatch(member, ChatConstants.PARTICIPANT_LEAVE);

      //Socket.emit(Constants.Sockets.Events.Leaver, member);
    }

    addMemberTyping(member) {
      dispatch(member, ChatConstants.TYPING_START);
      
      //Socket.emit(Constants.Sockets.Events.StartTyping, member);
    }

    removeMemberTyping(member) {
      dispatch(member, ChatConstants.TYPING_END);
      
      //Socket.emit(Constants.Sockets.Events.EndTyping, member);
    }

    openSession(maximum) {
      dispatch(maximum, ChatConstants.START_SESSION);
      
      //Socket.emit(Constants.Sockets.Events.Open, maximum);
    }

    closeSession() {
      dispatch(null, ChatConstants.END_SESSION);

      //Socket.emit(Constants.Sockets.Events.Close);
    }
}


